<template>
    <div v-if="ready" style='position:relative;'>
        <div class="col-12 flex" style='padding-left:0'>
            <div class="col-6" style='padding-left:0' v-if="show_form">
                <form id='form' enctype="multipart/form-data" @submit.prevent="handleSubmit">        
                    <div class="card">
                        <div class="card-header">
                            <strong>Application Form</strong>
                        </div>    
                        <div class="card-body" style='padding-left:0;padding-right:0'>
                            <div class="col-12 flex" style='margin-bottom:1rem'>
                                <div class="col-5">Reasons for the leave</div>
                                <div class="col-7">
                                    <input type="text" class="form-control" v-model="reason" placeholder="Enter Reason(s) for the leave"/>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-bottom:1rem'>
                                <div class="col-5">Leave Type</div>
                                <div class="col-7">
                                    <select class="form-control select"  v-model="l_type">                                        
                                        <option value='event'>Event</option>
                                        <option value='offical'>Official Leave</option>
                                        <option value='casual'>Casual Leave</option>
                                        <option value='sick'>Sick Leave</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-bottom:1rem'>
                                <div class="col-5">From</div>
                                <div class="col-7 flex">
                                    <!--
                                    <select class="form-control" multiple v-model="sel_date">
                                        <option v-for="d in date_list">{{ d.date }}</option>
                                    </select>-->
                                    <input type="date" class="form-control date col-7" v-model="st_date"/>
                                    <input type="time" class="form-control col-5" v-model="st_time"/>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-bottom:1rem'>
                                <div class="col-5">To</div>
                                <div class="col-7 flex">
                                    <!--
                                    <select class="form-control" multiple v-model="sel_date">
                                        <option v-for="d in date_list">{{ d.date }}</option>
                                    </select>-->
                                    <input type="date" class="form-control date col-7" v-model="ed_date"/>
                                    <input type="time" class="form-control col-5" v-model="ed_time"/>
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-bottom:1rem' v-if="!signed">
                                <div class="col-5">
                                    <label>    
                                        Security Code : 
                                    </label>
                                </div>
                                <div class="col-7 flex">
                                    <input type="password" v-model='pwd' onfocus="this.value=''" class="form-control col-7"/>                   
                                    <button type="button" class="form-control col-5" style='padding-left:0' ref="submitcode" @click="post_ssc">Sign now !</button>  
                                </div>
                            </div>
                            <div class="col-12 flex" style='margin-bottom:1rem' v-if="signed">
                                <div class="col-5">
                                    <label>    
                                        Signature 
                                    </label>
                                </div>
                                <div class="col-7 flex">
                                    <img :src="sign_data[0].signature" style="width:auto;height:5em;border-bottom:1px solid black;margin-left:0.5em"/>  
                                </div>
                            </div>
                        </div>
                        <div style='margin-left:2rem;margin-bottom:1rem'  v-if="message!=''">{{ message }}</div>
                        <div class="card-footer">
                            <button type="submit" class="btn btn-primary btn-sm">
                                <i class="fa fa-paper-plane"></i> Submit
                            </button>
                            <button type="reset" class="btn btn-danger btn-sm">
                                <i class="fa fa-ban"></i> Cancel
                            </button>
                        </div>
                    </div>    
                </form>   
            </div>
            <div class="col-6">
                <div class="card" style='padding-left:0'>
                    <div class="card-header">
                        <strong>Application Records</strong>
                    </div>    
                    <div class="card-body" style='padding:1.5rem 0 0 0'>
                        <v-data-table
                            :headers="headers"
                            :items="app_list"
                            :search="search"
                            class="elevation-1"
                            :rows-per-page-items="[10]"
                            v-if="hv_record"                
                            >
                        <template v-slot:items="props">
                            <td><a href="" :id= props.item.id v-on:click.prevent="open_form(props.item.id)">View</a></td>
                            <td>{{ props.item.app_date }}</td>
                            <td>{{ props.item.st_date }}</td>
                            <td>{{ props.item.ed_date }}</td>
                            <td>{{ props.item.reason }}</td>
                            <td>{{ props.item.leave_type }}</td>
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>
                        
                        </template>
                        </v-data-table>  
                    </div>
                </div>
            </div>
        </div>  
    </div>   
</template>
<script>
export default {
    data(){
        return{
            l_type:'sick',
            st_time:'07:55',
            ed_time:'15:45',
            st_date:'',
            ed_date:'',
            show_form:true,
            hv_record:false,
            app_list:[],
            user_check:'',
            message:'',
            sign_data:[],
            signed:false,
            pwd:'',
            reason:'',
            sel_date:'',
            date_list:[],
            ready:false,
            tdy_date:'',
            headers:[
                { text: 'Action',value :'id',sortable: false},
                { text: 'Application Date',value:'app_date',sortable: false},
                { text: 'From',value:'st_date',sortable: false},
                { text: 'To',value:'ed_date',sortable: false},
                { text: 'Reason',value:'reason',sortable: false},
                { text: 'Leave Type',value:'leave_type',sortable: false},
            ]
        }
    },   
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                if(!(this.user_check=='set')){
                    this.$router.push('../../Procurement/user_setup');
                }
            }
        )
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.tdy_date = today;
        await this.fetch_leave_date();
        await this.fetch_form();
        this.ready = true;
    },
    async mounted(){
        
    },
    methods:{
        open_form(id){
            window.open("src/subsitude/my_record/backend/open_app_form.php?rid="+id);
        },
        async fetch_form(){
            await axios.get('subsitude/my_record/backend/app_form.php')
                .then(
                    (response)=>{
                        this.app_list = response.data;
                    }
                )
        },
        post_ssc(event){
            var formData = new FormData();
            formData.append('pwd',this.pwd);
            axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
            .then(
                (response)=>{
                    this.sign_data = response.data;
                    if(response.data[0].signature==undefined){
                        this.message = response.data;
                    } else {
                    this.signed=true;
                    this.message='';
                    }
                }
            )
        },
        async fetch_leave_date(){
            await axios.get('subsitude/my_record/backend/leave_date.php')
                .then(
                    (response)=>{
                        this.date_list = response.data;
                    }
                )
            this.hv_record = true;
        },
        handleSubmit(){
            if(this.signed==true){
                this.sel_date = this.st_date+","+this.ed_date;
                var formData = new FormData();
                formData.append('reason',this.reason);
                formData.append('date',this.sel_date);
                formData.append('st_time',this.st_time);
                formData.append('ed_time',this.ed_time);
                formData.append('type',this.l_type);
                axios.post('subsitude/my_record/backend/insert_app_form.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='OK'){
                                this.fetch_form();
                                this.$forceUpdate();
                                this.show_form=false;
                            } else {
                                console.log(response.data);
                            }
                       }
                    )
            } else {
                this.$alert('Please Sign before submit');
            }
        }
    }
}
</script>
<style scoped>
td{
    transition: all 0.3s ease-in-out;
}
th{
    font-size:0.95rem;
}
.inner_overlay{
    z-index: 5;
    width:65%;
    max-height:60%;
    position:absolute;
    margin-left:auto;
    margin-top:auto;
    margin-bottom:auto;
    margin-right:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.sub_tr{
    height:5rem!important;
}
</style>




